import { SocketIoService } from './../../services/socket-io/socket-io.service';
import { logging } from 'protractor';
import { UserService } from './../../services/user/user.service';
import { Socket } from 'ngx-socket-io';
import { User } from './../../models/user';
import { Component, OnInit } from '@angular/core';
import { Message } from 'src/app/models/message';

@Component({
  selector: 'app-chat-screen',
  templateUrl: './chat-screen.component.html',
  styleUrls: ['./chat-screen.component.scss']
})
export class ChatScreenComponent implements OnInit {
  user: User = new User();
  messages: Message[] = []
  isLoggedIn: boolean
  user1_IsTyping: boolean = false
  user2_IsTyping: boolean = false
  message: string
  user1 = new User()
  user2 = new User()
  constructor(private userService: UserService, private io: SocketIoService) {
    this.isLoggedIn = false;
  }

  ngOnInit(): void {
    this.messages = []
    this.fakeLogin()

  }

  login() {
    this.fakeLogin();
    this.isLoggedIn = true;
  }

  fakeLogin() {

    this.user1.username = 'Dark Vador'
    this.user2.username = 'Skywalker'
  }
  isTyping(user, type, evt?) {
    if (type === 'in') {
      if (user === '0') { this.user2_IsTyping = evt }
      if (user === '1') { this.user1_IsTyping = evt }
    }
    if (type === 'out') {
      if (user === '0') return this.user1_IsTyping;
      if (user === '1') return this.user2_IsTyping;
    }
  }
  getMessage(evt) {
    this.messages.push(evt)
  }

}
