export class User {
  username: string;
  password: string;
  messageError: string;

  canLogin(): boolean {
    if (this.username === undefined || this.username === null || this.username.trim() === "") {
      this.messageError = "Please provide username";
      return false
    }
    if (this.username === undefined || this.username === null || this.username.trim() === "") {
      this.messageError = "Please provide password";
      return false
    }
    return true;
  }
}
