import { Component, ElementRef, HostListener, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs';
import { UtilsService } from 'src/app/services/utils/utils.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  @ViewChild('presentation') presentationElement: ElementRef
  @ViewChild('prev') prevIcon: ElementRef
  @ViewChild('next') nextIcon: ElementRef
  @ViewChild('skillsPane') slidePane: ElementRef
  skills: any[] = new Array(45)
  img = ['../../../assets/images/icons/angular.png']
  sliderData = [
    {
      title: "What I used to work with 0"
    },
    {
      title: "Who Am'I ?"
    },
    {
      title: "What I used to work with 2"
    }
  ]
  sliderHeaderText = "What I used to work with"
  currentIndex = 0
  aboutText = ""
  aboutTextInterval = null
  constructor(private renderer2: Renderer2, private utilsService: UtilsService, private router: Router) { }

  ngOnInit(): void {
    this.skills = [
      { icon: 'angular.svg' },
      { icon: 'css3.svg' },
      { icon: 'figma.svg' },
      { icon: 'flutter.svg' },
      { icon: 'git.png' },
      { icon: 'html.png' },
      { icon: 'ionic.png' },
      { icon: 'js.png' },
      { icon: 'laravel.svg' },
      { icon: 'nodejs.svg' },
      { icon: 'photoshop.svg' },
      { icon: 'php.svg' },
      { icon: 'python.svg' },
      { icon: 'rxjs.svg' },
      { icon: 'sass.svg' },
      { icon: 'typescript.svg' },
    ]

    this.skills = this.skills.map(x => {
      x.name = x.icon.substring(0, x.icon.indexOf('.')).toUpperCase()
      return x;
    })

  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    this.renderer2.listen(this.nextIcon.nativeElement, "click", (e) => {

        if (this.currentIndex < 2) { this.currentIndex+=1}

        if(this.currentIndex === 1){
          window.clearInterval(this.aboutTextInterval)
          this.typingSimulator(300)}


      this.renderer2.addClass(this.slidePane.nativeElement, "slide-right")
    })
    this.renderer2.listen(this.prevIcon.nativeElement, "click", (e) => {

        if (this.currentIndex > 0) { this.currentIndex-=1}
      this.renderer2.removeClass(this.slidePane.nativeElement, "slide-right")
      this.renderer2.addClass(this.slidePane.nativeElement, "slide-left")
    })
  }

  typingSimulator(rate) {
    let lorem = this.utilsService.getLoremText()+':)'.split('')
    let i = 0
    let len = lorem.length
    let stopTyping = false
    this.aboutTextInterval = setInterval(() => {
      if (lorem[i] && !stopTyping) {
        const isTenMultiple = ((i % 10) / 1) === 0
        if (!isTenMultiple) {
          // stopTyping = true
          // this.showCursor()
        }
        this.aboutText += '_'
        setTimeout(() => {
          this.aboutText = this.aboutText.substring(0, this.aboutText.length - 1)
          this.aboutText += lorem[i]
          i++
        }, 10);

      }
    }, rate)
  }
  showCursor() {
    let arr = new Array(3)
    for (let i in arr) {
      this.aboutText += '_'
      setInterval(() => {
        this.aboutText = this.aboutText.substring(0, this.aboutText.length - 1);

      }, 100);
      return false
    }

  }
  get getSlideTitle() {
    return   this.sliderData[this.currentIndex].title


  }

  showSlide(i){
    return i === this.currentIndex
  }
}
