import { Message } from './../../models/message';
import { SocketIoService } from './../../services/socket-io/socket-io.service';
import { Component, OnInit, ViewChild, ElementRef, Input, Output, EventEmitter, SimpleChange, SimpleChanges, OnChanges } from '@angular/core';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit, OnChanges {
  @Input('username') username: string = ""
  @Input('isTyping') isTyping: boolean = false
  @Output() iAmTyping = new EventEmitter<boolean>()
  @Output() newMessage = new EventEmitter<Message>()
  @Input('messages') messages: Message[] = []
  message: string
  isLoggedIn: boolean
  isScrolling: boolean = false
  @ViewChild('messageBox') messageBox: ElementRef
  constructor(private socketService: SocketIoService) { }

  ngOnChanges(changes: SimpleChanges): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.

    this.isTyping = changes.isTyping ? changes.isTyping.currentValue : this.isTyping
    this.messages = changes.messages ? changes.messages.currentValue : this.messages
    setTimeout(() => {
      this.scrollToBottom()
    }, 0);
  }
  ngOnInit(): void {
    this.socketService.listen("message").subscribe(evt => {
    })

    // setInterval(() => {
    //   this.scrollToBottom()
    // }, 10);


  }

  sendMessage() {
    if (this.message.length === 0) return
    this.socketService.emit('message', {
      message: this.message
    })
    this.newMessage.emit({
      id: this.username,
      text: this.message,
      isCurrentUser: false,
      isTyping: false
    })
    this.message = ""
  }
  typingEvent(eventType: string) {
    if (eventType === 'focus') {
      this.iAmTyping.emit(true)
      this.isScrolling = false
    }
    if (eventType === 'blur') {
      this.iAmTyping.emit(false)
    }
    setTimeout(() => {
      this.scrollToBottom()
    }, 200);
  }
  scrollToBottom(params?) {
      this.messageBox.nativeElement.scroll({
        left: 0, top: this.messageBox.nativeElement.scrollHeight, behavior: 'smooth'
      })


  }

}
