import { Directive, Renderer2, SimpleChange, SimpleChanges, ViewContainerRef } from '@angular/core';
import { Input, Output } from '@angular/core';
import { HostBinding } from '@angular/core';
import { HostListener, ElementRef, EventEmitter } from '@angular/core';


interface HeaderAction {
  id,
  action
}


@Directive({
  selector: '[appWindowsCSS]'
})


export class ChatBubbleDirective {
  @Input() isTyping: boolean;
  @Input() isCurrentUser: boolean;
  @HostBinding('style.backgroundColor') bgColor: string;
  @HostBinding('style.color') color: string;
  @Output() windowsHeaderAction = new EventEmitter<HeaderAction>();
  greenColor: string = 'green';
  yellowColor: string = 'yellow';
  redColor: string = 'red';
  constructor(private elemenRef: ElementRef, private renderer2: Renderer2, private viewContainerRef: ViewContainerRef) { }

  @HostListener('click') clickOnWindow() {

  }
  ngOnInit() {
    //creaate the typing bubble
    let typingBubble = this.createElement('div')
    this.renderer2.addClass(typingBubble, 'typping-bubble')
    for(let i in new Array(3)){
      let span = this.createElement('span')
      this.renderer2.appendChild(typingBubble, span);
    }
    // this.renderer2.appendChild(this.)
    this.renderer2.addClass(this.elemenRef.nativeElement, 'chat-bubble')
  }
  ngAfterViewInit() {
  }

  createElement(name: string) {
    return this.renderer2.createElement(name)
  }
  addClass(element, name: string) {
    this.renderer2.addClass(element, name)
  }

}
