import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class UtilsService {
  loremText = "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Minima distinctio non, dignissimos aspernatur iste doloribus numquam veritatis eligendi dolor impedit ipsam esse quae voluptatibus! Nesciunt porro iure eveniet cumque? Labore!"
  constructor() { }

  getRandomColor() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }

  getLoremText() {
    return this.loremText;
  }
}
