import { ApiService } from './../../services/api/api.service';
import { debounceTime, distinctUntilChanged, map, startWith } from 'rxjs/operators';
import { Component, OnInit, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { fromEvent } from 'rxjs';

@Component({
  selector: 'app-url-shortener',
  templateUrl: './url-shortener.component.html',
  styleUrls: ['./url-shortener.component.scss']
})
export class UrlShortenerComponent implements OnInit {
  isTyping: boolean = false
  isLoading: boolean = false
  @ViewChild('searchInput') searchInput: ElementRef
  @ViewChild('shortUrlCopy') shortUrlCopy: ElementRef
  @ViewChild('modal') modal: ElementRef
  longUrl: string = ""
  search$: any
  shortUrl: string = "My shortened url...";
  urlObj: any
  showModal: boolean;
  validUrl: boolean;
  toastMessge: string;
  constructor(private apiService: ApiService, private renderer2: Renderer2) { }

  ngAfterViewInit() {
    this.search$ = fromEvent(this.searchInput.nativeElement, 'keyup').pipe(
      map((evt: any) => evt.target.value),
      startWith(''),
      debounceTime(100),
      distinctUntilChanged()
    )
    this.search$ = this.search$.subscribe(text => {
      const reg = new RegExp('(https?://)?([\\da-z.-]+)\\.([a-z.]{2,6})[/\\w .-]*/?');
      if (!reg.test(this.longUrl)) {
        this.validUrl = false;
      } else {
        this.validUrl = true;
      }
      console.log(this.validUrl)
    })
  }
  ngOnInit(): void {
    // this.showModal = true
  }

  shortenUrl() {
    if (!this.validUrl) {
      this.toastMessge = "Invalid url"
      this.showToast()
      return;
    }
    this.apiService.shortenUrl(this.longUrl).then((res: any) => {
      console.log(res)
      if(!res || res.status === "error"){
        this.toastMessge = "Connection error"
        this.showToast()
        return
      }
      if (res.data) { this.urlObj = res.data; this.shortUrl = res.data.shortUrl; return }
      this.shortUrl = "";
    })
  }

  copyUrl() {
    if (!this.urlObj) {
      this.toastMessge = "Please provide url"
      this.showToast()
      return
    }
    this.shortUrlCopy.nativeElement.select()
    this.shortUrlCopy.nativeElement.setSelectionRange(0, 99999)
    document.execCommand("copy")
    this.toastMessge = "Copied to clipboard"
    this.showToast()
  }

  previewPage() {
    this.showModal = true;
  }
  openPage() {
    if (!this.urlObj) {
      this.toastMessge = "Please provide url"
      this.showToast()
      return
    }
    const url = this.urlObj.shortUrl;
    window.open(url, '_blank')
  }

  showToast(){
    this.showModal = true

  if(this.modal){  this.renderer2.removeClass(this.modal.nativeElement, "animate")
    this.renderer2.addClass(this.modal.nativeElement, "animate")}
    setTimeout(()=>{
      this.showModal = false
    }, 3000)
  }

}
