import { MultiClipboardComponent } from './softwares/multi-clipboard/multi-clipboard.component';
import { UrlShortenerComponent } from './pages/url-shortener/url-shortener.component';
import { ChatComponent } from './components/chat/chat.component';
import { HomeComponent } from './pages/home/home.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChatScreenComponent } from './pages/chat-screen/chat-screen.component';
import { PortfolioComponent } from './pages/portfolio/portfolio.component';
import { GmailScrapperComponent } from './softwares/gmail-scrapper/gmail-scrapper.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'gscrapper',
    pathMatch: "full"
  },
  {
    path: 'home',
    component: HomeComponent
  },
  {
    path: 'portfolio',
    component: PortfolioComponent
  },
  {
    path: 'url-shortener',
    component: UrlShortenerComponent
  },
  {
    path: 'multi-clipboard',
    component: MultiClipboardComponent
  },
  {
    path: 'chat',
    component: ChatScreenComponent
  },
  {
    path: 'gscrapper',
    component: GmailScrapperComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
