    FormsModule,
    MatMenuModule,
    MatButtonModule,
    MatIconModule,
    DragDropModule,
    PdfViewerModule,
    MatInputModule,
    SocketIoModule.forRoot(config), HttpClientModule,
    MatSelectModule,MatFormFieldModule, ReactiveFormsModule
